import React, { Component } from "react";

class Info extends Component {
  render() {
    return (
      <section id="about" className="section is-centered has-text-centered columns" style={{paddingTop: "1.5rem", backgroundColor: "#f7faff"}}>
      <div className="column is-5">
        <a href="https://market.globatalent.com/register"><h2 className="title is-4" style={{color: '#335AA1'}}>{this.props.t("info.title")}</h2></a>
      </div>
      </section>
    )
  }
}

export default Info;